.pokemonList {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 100%;
}

.control-button{
    width: 100px;
    height: 50px;
    background-color: rgb(159, 157, 157);
    border-radius: 20px;
    padding: 10px;
    font-weight: bold;
    border: none;
    margin-inline: 5px;
}

.control-button:hover{
    opacity: 0.7;
}

.button-div{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    margin-top: 30px;
}

