.box
{
    width: 220px;
    height: 100%;
    background-color: green;
    border-radius: 20px;
    padding: 10px;
    margin: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.small-text
{
    font-size: 10px;
}

.pokemon-name {
    font-size: 20px;
    font-weight: bold;
    color: white;
}